@import url('https://fonts.googleapis.com/css2?family=DotGothic16&family=M+PLUS+Rounded+1c:wght@300&family=Reggae+One&&family=RocknRoll+One&display=swap');
.App {
  text-align: center;
  color: #fff;
}
.App-title{
  font-size: 2.5rem;
  font-weight: 800;
  text-shadow: 0.1rem 0.1rem 0.2rem rgb(196, 196, 196);
  margin: 1em;
  cursor: default;
  font-family: 'DotGothic16', sans-serif;
}
  .App-title:hover{
    color: pink;
    text-shadow: 0.1rem 0.1rem 0.2rem rgb(255, 0, 0);
    transition: 0.5s ease-out;
  }
.twittertimeline{
  width:30%;
  float: left;
  margin-left: 5%;
  border: 0.25rem solid lightblue;
  outline: 0.25rem solid #fff;
  border-radius: 1.3rem;
}

@media screen and (max-width: 1150px) {
  .twittertimeline{
    display: none;
  }
}